@import "../../global.scss";

.intro {
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
      }

    .left {
        flex: 0.5;
        overflow: hidden;
        .imgContainer {
            height: 600px;
            width: 600px;
            background-color: lightgray;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile{
                align-items: flex-start;
            }

            img {
                height: 90%;

                @include mobile{
                    height: 50%;
                  }
            }
        }
    }

    .right {
        flex: 0.5;

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            overflow: hidden;
            position: relative;

            @include mobile{
                padding-left: 0;
                align-items: center;
              }

            h1 {
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 40px;
                  }
            }

            h2 {
                font-size: 35px;
            }

            h3 {
                font-size: 30px;

                span {
                    font-size: inherit;
                    color: crimson;
                }

                @include mobile{
                    font-size: 20px;
                  }
            }
        }

        a {
            position: absolute;
            bottom: 10px;
            left: 70%;

            img {
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }
        }
    }


}